.Screen{
  min-height: 100vh;
  background-color: #131a2c;
}
.row {
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
}
.col {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
}
.ButtonBar{
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: right;
  font-size: calc(10px + 2vmin);
  background-color: #131a2c;
  color: white;
}
.ButtonDiv{
  margin: 15px 25px;
}

.icon{
  height:50px;
  width: 50px;
}